$("._form").submit(function (e) {
    e.preventDefault()
    let nameIsValid = false
    let phoneIsValid = false
    let agreeIsValid = false


    let phone = $(this).find("input[name='phone']")
    let name = $(this).find("input[name='name']")
    let agree = $(this).find("input[name='agree']")


    if ($(name).val() !== "") {
        nameIsValid = true
        $(name).parents(".fg").removeClass("_error")
    } else {
        nameIsValid = false
        $(name).parents(".fg").addClass("_error")
    }

    if ($(phone).val() !== "") {
        phoneIsValid = true
        $(phone).parents(".fg").removeClass("_error")
    } else {
        phoneIsValid = false
        $(phone).parents(".fg").addClass("_error")
    }

    if ($(agree).prop("checked")) {
        agreeIsValid = true
        $(agree).parents(".m-check").removeClass("_error")
    } else {
        agreeIsValid = false
        $(agree).parents(".m-check").addClass("_error")
    }




    if(!phoneIsValid || !nameIsValid || !agreeIsValid){
        return
    }


    let formData = {
        name: name.val(),
        phone: phone.val(),
    }


    $(this).addClass("_success")
    $.ajax({
        type: "POST",
        url: "php/send.php",
        data: formData,
        success: function() {

        }
    })
})
