function whatSlider() {
    var swiper = new Swiper('.what-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        centeredSlides: true,
        on: {
            init: function () {
                this.slideTo(1)
            },
        },
    })
}

function projectsSlider() {
    var swiper = new Swiper('.projects .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        navigation: {
            nextEl: '.projects .swiper-button-next',
            prevEl: '.projects .swiper-button-prev',
        },
        scrollbar: {
            el: '.projects .swiper-scrollbar',
            draggable: true,
            snapOnRelease: true,
            dragSize: 'auto',
        },
    })
}

function reviewsSlider() {
    var swiper = new Swiper('.reviews .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        loop: true,
        navigation: {
            nextEl: '.reviews .swiper-button-next',
            prevEl: '.reviews .swiper-button-prev',
        },
        scrollbar: {
            el: '.reviews .swiper-scrollbar',
            draggable: true,
            snapOnRelease: true,
            dragSize: 'auto',
        },
    })
}


whatSlider()
projectsSlider()
reviewsSlider()
